import {Component, HostListener, OnInit} from '@angular/core';
import {MediaMatcher} from '@angular/cdk/layout';
import {MatDialog} from '@angular/material/dialog';
import {Router} from '@angular/router';
import {TokenService} from 'src/app/services/authentication/token/token.service';
import {DialogComponent} from '../shared/dialog/dialog.component';
import {UserSessionService} from 'src/app/services/authentication/user/user-session.service';
import {UserIdleService} from 'angular-user-idle';
import {fromEvent, merge} from 'rxjs';
import {SharedInterfaceService} from 'src/app/services/utils/shared/shared-interface.service';
import {DialogClass} from '../../utils/dialog-data';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  public innerWidth: any;
  public innerHeight: any;
  public showMenu = false;
  public showLogo: boolean;
  public showItems = true;
  public user: any;

  constructor(
    private router: Router,
    private tokenService: TokenService,
    private userAuth: UserSessionService,
    private userIdleService: UserIdleService,
    private sharedInterfaceService: SharedInterfaceService,
    public media: MediaMatcher,
    public dialog: MatDialog
  ) {
    this.userIdleService.setCustomActivityEvents(merge(
      fromEvent(window, 'mousemove'),
      fromEvent(window, 'resize'),
      fromEvent(window, 'scroll'),
      fromEvent(document, 'keydown'),
      fromEvent(document, 'touchstart'),
      fromEvent(document, 'touchend'),
    ));
    this.userIdleService.setConfigValues({idle: 600, timeout: 120, ping: 2});
  }

  @HostListener('window:resize', ['$event'])
  onResize() {
    this.innerHeight = window.innerHeight - 94;
    this.innerWidth = window.innerWidth;
  }

  ngOnInit() {
    /** Configuration Inactivity in Application */
    this.userIdleService.startWatching();
    this.userIdleService.onTimerStart().subscribe((count) => {
      this.sharedInterfaceService.putTimeInactivity(count);
      if (count === 1)
        this.openDialog(new DialogClass(401, 'Sesin Prxima a Caducadar', '', 5), false);
    });
    this.userIdleService.onTimeout().subscribe(() => {
      this.dialog.closeAll();
      this.logout();
    });
    /** Configuration resize application */
    this.innerHeight = window.innerHeight - 94;
    this.innerWidth = window.innerWidth;
    /** Get data items menu */
  }

  logout() {
    this.userAuth.deleteUser();
    this.tokenService.deleteToken();
    this.router.navigateByUrl('/signIn');
  }

  openDialog(data: DialogClass, close: boolean) {
    this.dialog.open(DialogComponent, {width: '350px', data: data, disableClose: close});
  }
}
